var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"titre1 row select"},[_c('div',{staticClass:"col-sm-3 select__nav"},[_c('p',[_vm._v("FORMATION DIPLOMANTE SESHW")]),_c('p',[_vm._v("Information obligatoire avant inscription définitive")])])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 1 Motivation et finalité ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" L’Institut Kemetmaat Cheikh Anta Diop est un département de l’Association Kemetmaat ouvert depuis 2012. L’association est affiliée à l’organisation communautaire Afrocentricity International ; laquelle est présente dans une cinquantaine de pays du continent et de la diaspora. Dans le cadre du courant de la renaissance africaine qui monte en puissance, l’IKCAD s’est donné pour vocation de former les futurs cadres et acteurs de cette révolution culturelle. Aussi l’IKCAD propose aux jeunes africains et africaines un programme d’enseignement et de recherche de seshw et Seshw Nsw de kamitologie en ligne. Le Seshw est un grade d’enseignement supérieur de l’IKCAD acquis au terme d’un parcours de 4 semestres académiques d’études. Il est l’équivalent du Master dans le système éducatif officiel. Le Seshw Nsw est un grade d’enseignement supérieur de l’IKCAD acquis au terme d’un parcours de 4 semestres académiques d’études et de recherche après l’obtention du Seshw. Il est l’équivalent du Doctorat dans le système éducatif officiel. Le seba est un certificat délivré dans le cadre du programme aux personnes ayant validées 1 cours semestriel d’un volume horaire de 24 heures. La kamitologie est le domaine des études africaines conduites dans une perspective africaine. Il s’agit de l’application de la théorie de l’afrocentricité, fondée par le professeur Kete Asante, dans le champ épistémologique. Le professeur Ama Mazama grande figure de l’école de l’afrocentricité est la conceptrice de ce programme académique de kamitologie de haut niveau dont la mise en œuvre est assurée, sous sa direction, par une équipe pédagogique qualifiée. L’enjeu de ce programme est de contribuer à la renaissance culturelle des peuples négro-africains telle que la définie le savant Cheikh Anta Diop : « Les nouvelles humanités africaines devront reposer sur les fondements de la culture égypto-nubienne de même que les humanités occidentales s’appuient sur la culture gréco - latine antique. Sans référence systématique à l’Egypte dans tous les domaines de la culture, il ne sera pas possible de bâtir un corps de sciences humaines ... il s’agit de la redécouverte d’un soi culturel et non d’une imitation servile ». CHEIKH ANTA DIOP (Alerte sous les tropiques, présence africaine, Edition 1990). La création de ce programme est une expression concrète de la volonté des africains conscients de s’affranchir du carcan institutionnel de l’eurocentrisme et de créer un cadre afrocentrique d’enseignement et de recherche supérieur. Il s’agit de rompre avec toutes les formes de tutelles intellectuelles et académiques étrangères héritées de la période des déportations et de la colonisation et d’affirmer la présence et la vitalité de la pensée, des sciences, des techniques et des arts négro-africains (kamit) dans le monde d’aujourd’hui. Les diplômes de l’IKCAD sont donc des diplômes libres. «VOIS, IL N’EXISTE PAS DE PROFESSION SANS CHEF, EXCEPTE LE SCRIBE: CEST LUI LE CHEF». MET, Sankofa, Don d'Ernst Anspach, 1994, 1994.312.16. MET, Fonds Rogers et don Edward S. Harkness, 1920, 20.3.11. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 2 Finalité ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Il s’agit techniquement de doter l’étudiant d’un important bagage intellectuel par une formation de base pluridisciplinaire qui associe principalement les domaines des langues, de l’histoire, des lettres, de la philosophie, de la cosmologie, de la sociologie, de l’art dans un paradigme autoréférencé africain. Grace à l’étude de ces grandes disciplines dans une approche afrocentrique, l’apprenant acquiert de solides méthodes de travail, une grande culture générale, une claire conscience historique, un amour pour le service à l’Afrique, un grand sens critique et un sens créatif très important. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 3 Public cible ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Le programme s’adresse, en formation initiale, à des étudiants de niveau licence du système officiel d’enseignement désireux d’acquérir une spécialité professionnelle ou recherche dans une perspective afrocentrique. Il s’agit pour eux de poursuivre leurs études supérieures tout en se reconvertissant au paradigme pédagogique afrocentrique. En formation continue, le programme de kamitologie offre l’opportunité à des professionnels et à des chercheurs de se reconvertir dans la nouvelle économie de la renaissance africaine. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 4 Les diplômes et perspective d’emploie ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Dans quelques années l’Afrique sera la première puissance démographique mondiale. Cette montée en puissance s’accompagnera d’une montée de la conscience positive de l’Afrique. Ce phénomène déjà palpable suscitera des demandes qu’il faut pouvoir anticiper et auxquelles on doit se préparer à répondre. Par exemple dans le domaine éducatif il faudra des enseignants équipés de solides connaissances de l’histoire et de la culture et des langues de notre continent. Tous les domaines de l’existence sont concernés ; la politique, l’économie, l’architecture, les arts créatifs etc. Les débouchés des formations de l’IKCAD sont celles offertes par ces prometteuses perspectives. Les diplômes de l’IKCAD sont par conséquent novateurs et uniques au monde car ils anticipent sur la société panafricaine de demain. Les débouchés sont fonctions des spécialités du Seshsw et du Seshw Nsw : créateurs d’écoles afrocentriques, enseignants et formateurs, formateurs de formateurs, Ingénieur de formation, responsable du dispositif de formation, créateurs d’entreprises ou d’activités économiques afrocentrées, leaders d’associations civiles ou politiques, philosophes, théologien kamit, chercheur en afrologie, historien de l’art, historien, conférencier, communication, sociologue, journaliste, géostratège, consultant et conseillé specialisé. «JE FERAI QUE TU AIMES LES ÉCRITS PLUS QUE TA MÈRE. JE FERAI QUE LEUR BEAUTÉ ENTRE EN TON VISAGE (EN TOI) CAR ELLE (LA FONCTION DE SCRIBE) EST PLUS GRANDE QUE TOUTE AUTRE FONCTION. IL N’Y A PAS SON PAREIL SUR TERRE...». MET, Senemout,Don anonyme, 1931, 31.4.2. MET, Djedkhonsuefankh, son of Khonsumes and Taat, DT318224. Scribe boy by Sanio on DeviantArt. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 5 Le dispositif de formation ouverte à distance ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Les cours sont proposés sur un format en ligne strict. Toutes les conférences sont électroniques et sont accessibles par ordinateur. La plate-forme numérique de l’IKCAD utilise 4 outils principaux : - Une application de formation en ligne où sont disponibles la vidéo, le manuel et les exercices classés par séquence pédagogique. - Une application pour les séances de webconférences en live - Un groupe WhatsApp qui regroupe les étudiants d’une même classe et leurs formateurs. - Une boite Gmail pour les correspondances entre formateurs et auditeurs Les cours exigent une lecture intensive et les étudiants doivent être prêts à écrire ou à discuter après avoir lu les textes requis. Les enseignants attribueront normalement des lectures et celles-ci seront discutées, débattues, critiquées de manière constructive et évaluées dans le contexte afrocentrique. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 6 La direction du programme ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Le programme d’études supérieures en études africaines Seshw et Seshw Nsw est dirigé par le professeur Ama Mazama ; « Imy r Sebayt » (directrice de l’enseignement) à l’IKCAD. A ce titre elle siège à tous les comités de thèse de l’IKCAD. Elle est docteure en linguistique à l’Université de la Sorbonne Nouvelle. Elle est professeure d’Etudes Africaines aux USA. Elle a publié de nombreux ouvrages et articles en français et en anglais sur l’Afrocentricité. Elle a notamment coédité l’Encyclopédie des études noires et l’Encyclopédie de la religion africaine. Elle possède une grande expérience dans la direction des doctorats en études africaines -américaines. Ses étudiants ont été cités pour leur érudition et leur leadership dans le domaine des études Africaines par des revues et des institutions depuis plus de deux décennies. L’Imy r Sebayt conduit une équipe d’enseignants et de chercheurs tous compétents en kamitologie dans leur spécialité respective. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 7 Organisation et contenu de la formation . ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Le programme se déroule sur 8 semestres répartis en deux cycles. Chaque fin de cycle est sanctionnée par un diplôme. Premier cycle de kamitologie Au terme des 4 premiers semestres (cycle 1), l’étudiant doit avoir une bonne connaissance des théories et méthodes afrocentriques, de l’histoire et la civilisation africaine, de la langue classique africaine (mdw ntr), de la linguistique historique africaine, de la philosophie africaine. La fin du cycle 1 est sanctionné par le titre de Sesh (Master). Deuxième cycle de kamitologie Le cycle 2 comprend 4 semestres. Il est centré sur la formation et la pratique à la recherche dans la spécialité choisie par l’étudiant. Le Seshw Nsw (doctorat) est délivré à l’issue de la présentation de la thèse de fin d’études. Le cycle 2 est sanctionné par le titre de Sesh Nsw (Docteur). «SI UN SCRIBE EST PROMU À UNE DIGNITÉ QUELCONQUE, IL A, A CONSULTÉ DES LIVRES». ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 8 Programme ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" PROGRAMME DE SESH NSW (DOCTORAT) EN KAMITOLOGIE 2022-2025 Semestre 3 KMT 7077 Histoire et civilisations de l’époque impériale Semestre 4 Cycle 1 SESHW (MASTER) KMT 7007 Théorie et méthodologie de l’afrocentricité KMT 7037 Les grands mouvements de la pensée africaine Semestre 1 KMT 7017 Historiographie diopienne Semestre 2 KMT 7047 Préhistoire et Histoire ancienne de l’Afrique KMT 7027 Initiation à la langue des Mdw Ntr: Écriture et Grammaire KTM 7057 Initiation à la langue des Mdw Ntr: Ecriture et Grammaire KMT 7067 Spiritualité et renais- sance africaines KMT 7097 Histoire et culture de la diaspora africaine KMT 7107 Les grandes invasions de l’Afrique KMT 7087 Initiation à la Langue des Mdw Ntr : Ateliers de Lecture et Traduction de Textes KMT 7117 Langue Pan-Africaine Moderne: Le Kiswahili Cycle 2 SESHW - NSW (DOCTORAT) KMT 7127 Semestre 5 Semestre 7 Semestre 8 Séminaire de Recherche en Kamitologie KMT 7137 Littérature Africaine et Diasporique KMT 7147 Rekh Necher et la Tradition Scientifique Africaine KMT 7167 Elaboration de la Thèse en Kamitologie KMT 7177 Soutenance de Thèse en Kamitologie Semestre 6 KMT 7157 Elaboration & Soutenance du Mémoire de Pré- Thèse en Kamitologie Programme 27 Décembre 2021 «ON FAIT TOUT CE QUE TU DIS QUAND TU ES INSTRUIT DANS LES LIVRES, RENDS-TOI LES LIVRES FAMILIERS, METS-LES DANS TON COEUR. TOUT CE QUE TU DIRAS DEVIENDRA EXCELLENT» ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 9 Autres activités académiques ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Conférence annuelle. Les doctorants doivent participer au symposium annuel de l’IKCAD sur les études africaines. Il s’agit d’un symposium d’une journée où les doctorants discutent de leurs recherches avec le public. Immersion dans les communautés villageoises. Les étudiants sont tenus de suivre une immersion culturelle intensive dans un village africain. La certification de cette exigence doit être présentée avant que l’étudiant n’ait obtenu son diplôme de Sesh. Thèse de Seshw Nsw. La thèse est une étude afrocentrique originale qui démontre la connaissance des méthodes de recherche et une maîtrise de son domaine d’intérêt principal. Il s’agit d’un travail qui a fait l’objet d’une enquête rigoureuse sous la supervision directe d’un enseignant. La plupart des mémoires feront entre 175 et 225 pages. Toutes les thèses retenues seront publiées par l’IKCAD. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 10 Déroulement des cours ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Chaque séance de cours sera d’une durée 2 heures. Les étudiants inscrits prendront 3 cours par semestre. En d’autres termes ceci équivaut à un volume horaire de 72 heures d’enseignement par semestre. La période entre les deux semestres est réservée aux travaux personnels, conférences et autres activités académiques. ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 11 Les formalités d’admissions aux cycles du programme ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Les étudiants sont admis deux fois par an. La date limite de présentation des demandes pour le premier semestre 2022 est le 31 décembre 2021 pour une rentrée qui se fera fin janvier. La date limite de présentation des demandes pour le deuxième semestre 2022 est le 30 juin 2022 pour une rentrée qui se fera début Juillet 2022. L’étudiants doit justifier un niveau de formation universitaire équivalent à la licence. Cependant l’admission peut se faire à la suite d’un entretien d’évaluation et sur la base d’un dossier à fournir. Les étudiants titulaires des certificats afrologiques de l’IKCAD peuvent être dispensé de certains enseignements. Tous les renseignements sur l’inscription peuvent être obtenus auprès du coordinateur de l’IKCAD ou de son secrétariat. «IMITEZ VOS PÈRES ET VOS ANCETRES, ET RÉUSSISSEZ PAR LE SAVOIR. LEURS PAROLES SONT RESTÉES ÉCRITES. OUVREZ-LES ET LISEZ-LES, AFIN D’IMITER LEUR SAVOIR. CELUI QUI EST COMPÉTENT DEVIENDRA SAVANT.» ")])]),_c('div',{staticClass:"more"},[_c('p',[_vm._v(" 12 Le planning ")]),_c('a',{staticClass:"more-text",attrs:{"tabindex":"0","href":""}},[_c('span',{staticClass:"plus"},[_vm._v("Lire plus")]),_c('span',{staticClass:"moins"},[_vm._v("Lire moins")])]),_c('p',{staticClass:"hidetext"},[_vm._v(" Le planning du premier semestre 2022 est disponible auprès du secrétariat. Egyptian Writing Board is a photograph by Metropolitan Museum Of Art/science Photo Library. Image de couverture > Bloc statue de ˁnḫ wn nfr periode 690-650 avant notre ère. MET, ,Purchase, Lila Acheson Wallace Gift and Rogers, Fletcher, Harris Brisbane Dick, and Louis V. Bell Funds and Joseph Pulitzer Bequest, 1993. Djeouty,au British Museum (Londres). EA 38 Steven G. Johnson. ")])])])}]

